button{margin-right:10px;text-align:center}.loginmodal-container{padding:30px;max-width:350px;width:100% !important;background-color:#F7F7F7;margin:0 auto;border-radius:2px;-webkit-box-shadow:0px 2px 2px rgba(0,0,0,0.3);box-shadow:0px 2px 2px rgba(0,0,0,0.3);overflow:hidden;font-family:roboto}.loginmodal-container h1{text-align:center;font-size:1.8em;font-family:roboto}.loginmodal-container input[type=submit]{width:100%;display:block;margin-bottom:10px;position:relative}.loginmodal-container input[type=text],input[type=password]{height:44px;font-size:16px;width:100%;margin-bottom:10px;-webkit-appearance:none;background:#fff;border:1px solid #d9d9d9;border-top:1px solid #c0c0c0;padding:0 8px;-webkit-box-sizing:border-box;box-sizing:border-box;-moz-box-sizing:border-box}.loginmodal-container input[type=text]:hover,input[type=password]:hover{border:1px solid #b9b9b9;border-top:1px solid #a0a0a0;-webkit-box-shadow:inset 0 1px 2px rgba(0,0,0,0.1);box-shadow:inset 0 1px 2px rgba(0,0,0,0.1)}.loginmodal{text-align:center;font-size:14px;font-family:'Arial', sans-serif;font-weight:700;height:36px;padding:0 8px}.loginmodal-submit{border:0px;color:#fff;text-shadow:0 1px rgba(0,0,0,0.1);background-color:#4a4a4a;padding:17px 0px;font-family:roboto;font-size:14px}.loginmodal-submit:hover{border:0px;text-shadow:0 1px rgba(0,0,0,0.3);background-color:#357ae8}.loginmodal-container a{text-decoration:none;color:#666;font-weight:400;text-align:center;display:inline-block;opacity:0.6;-webkit-transition:opacity ease 0.5s;-o-transition:opacity ease 0.5s;transition:opacity ease 0.5s}.login-btn{text-align:center;margin-top:50px}.button{line-height:55px;padding:0 30px;background:#004a80;color:#fff;display:inline-block;font-family:roboto;text-decoration:none;font-size:18px}



